"use client"

import { useState } from "react"
import { Plus, Minus, Wine, Droplets, Cake, Cookie, ZoomIn } from "lucide-react"
import type { Extra, ExtraItem } from "@/lib/types"
import { ImageModal } from "@/components/image-modal"

interface ExtrasSelectorProps {
  extras: Extra[]
  availableExtras?: Record<string, ExtraItem[]>
  onUpdate?: (extras: Extra[]) => void
  disabled?: boolean
}

const categoryConfig: Record<string, { label: string; icon: typeof Wine }> = {
  drink: { label: "Напитки", icon: Wine },
  sauce: { label: "Соусы", icon: Droplets },
  dessert: { label: "Десерты", icon: Cake },
  snack: { label: "Закуски", icon: Cookie },
}

export function ExtrasSelector({ extras, availableExtras, onUpdate, disabled }: ExtrasSelectorProps) {
  const [expandedCategory, setExpandedCategory] = useState<string | null>(null)
  const [imageModal, setImageModal] = useState<{
    image: string
    name: string
    price: number
    ingredients?: string
    description?: string
    nutrition?: { calories: number; protein: number; fats: number; carbs: number; weight?: number }
  } | null>(null)

  const getExtraQuantity = (name: string) => {
    return extras.find((e) => e.name === name)?.quantity || 0
  }

  const updateExtra = (item: ExtraItem, delta: number) => {
    if (disabled || !onUpdate) return
    const existing = extras.find((e) => e.name === item.name)
    const currentQty = existing?.quantity || 0
    const newQty = Math.max(0, currentQty + delta)

    if (newQty === 0) {
      onUpdate(extras.filter((e) => e.name !== item.name))
    } else if (existing) {
      onUpdate(extras.map((e) => (e.name === item.name ? { ...e, quantity: newQty } : e)))
    } else {
      onUpdate([...extras, { name: item.name, price: item.price, quantity: newQty, image: item.image }])
    }
  }

  const getCategoryItems = (category: string): ExtraItem[] => {
    return availableExtras?.[category] || []
  }

  const getCategoryTotal = (category: string) => {
    return getCategoryItems(category).reduce((sum, item) => {
      const qty = getExtraQuantity(item.name)
      return sum + item.price * qty
    }, 0)
  }

  const getCategoryCount = (category: string) => {
    return getCategoryItems(category).reduce((sum, item) => sum + getExtraQuantity(item.name), 0)
  }

  const categories = Object.keys(availableExtras || {}).filter(
    (cat) => categoryConfig[cat] && getCategoryItems(cat).length > 0,
  )

  if (disabled) {
    const selectedExtras = extras.filter((e) => e.quantity > 0)
    if (selectedExtras.length === 0) {
      return (
        <div>
          <h3 className="font-semibold text-sm text-muted-foreground mb-2">Дополнения к заказу</h3>
          <p className="text-xs text-muted-foreground">Нет дополнений</p>
        </div>
      )
    }

    return (
      <div>
        <h3 className="font-semibold text-sm text-muted-foreground mb-2">Дополнения к заказу</h3>
        <div className="space-y-2">
          {selectedExtras.map((extra) => {
            const fullExtraData = Object.values(availableExtras || {})
              .flat()
              .find((e) => e.name === extra.name)

            return (
              <div key={extra.name} className="flex items-center gap-3 p-2 rounded-lg bg-muted/20">
                {extra.image && (
                  <div
                    className="relative w-10 h-10 rounded-lg overflow-hidden bg-muted flex-shrink-0 cursor-pointer group"
                    onClick={() =>
                      setImageModal({
                        image: extra.image || "",
                        name: extra.name,
                        price: extra.price,
                        ingredients: fullExtraData?.ingredients,
                        description: fullExtraData?.description,
                        nutrition: fullExtraData?.nutrition,
                      })
                    }
                  >
                    <img
                      src={extra.image || "/placeholder.svg"}
                      alt={extra.name}
                      className="w-full h-full object-cover"
                    />
                    <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
                      <ZoomIn className="w-3 h-3 text-white" />
                    </div>
                  </div>
                )}
                <div className="flex-1 min-w-0">
                  <p className="font-medium text-sm truncate">{extra.name}</p>
                </div>
                <div className="text-sm text-muted-foreground">x{extra.quantity}</div>
                <div className="text-sm font-medium text-primary">{extra.price * extra.quantity} ₽</div>
              </div>
            )
          })}
        </div>

        {imageModal && (
          <ImageModal
            image={imageModal.image}
            name={imageModal.name}
            ingredients={imageModal.ingredients}
            description={imageModal.description}
            nutrition={imageModal.nutrition}
            onClose={() => setImageModal(null)}
          />
        )}
      </div>
    )
  }

  if (categories.length === 0) {
    return (
      <div className="space-y-3">
        <h3 className="font-semibold text-sm text-muted-foreground mb-2">Дополнения к заказу</h3>
        <p className="text-sm text-muted-foreground">Загрузка дополнений...</p>
      </div>
    )
  }

  return (
    <div className="space-y-3">
      <h3 className="font-semibold text-sm text-muted-foreground mb-2">Дополнения к заказу</h3>

      {categories.map((category) => {
        const config = categoryConfig[category]
        if (!config) return null

        const Icon = config.icon
        const count = getCategoryCount(category)
        const total = getCategoryTotal(category)
        const isExpanded = expandedCategory === category
        const items = getCategoryItems(category)

        return (
          <div key={category} className="border border-border rounded-lg overflow-hidden">
            <button
              onClick={() => setExpandedCategory(isExpanded ? null : category)}
              className="w-full flex items-center justify-between p-3 bg-muted/30 hover:bg-muted/50 transition-colors"
            >
              <div className="flex items-center gap-2">
                <Icon className="w-4 h-4 text-primary" />
                <span className="font-medium text-sm">{config.label}</span>
                {count > 0 && (
                  <span className="bg-primary text-primary-foreground text-xs px-2 py-0.5 rounded-full">{count}</span>
                )}
              </div>
              {total > 0 && <span className="text-sm font-medium text-primary">{total} ₽</span>}
            </button>

            {isExpanded && (
              <div className="p-3 space-y-2 bg-background">
                {items.map((item) => {
                  const qty = getExtraQuantity(item.name)
                  return (
                    <div key={item.name} className="flex items-center gap-3 p-2 rounded-lg bg-muted/20">
                      <div
                        className="relative w-12 h-12 rounded-lg overflow-hidden bg-muted flex-shrink-0 cursor-pointer group"
                        onClick={() =>
                          setImageModal({
                            image: item.image || "",
                            name: item.name,
                            price: item.price,
                            ingredients: item.ingredients,
                            description: item.description,
                            nutrition: item.nutrition,
                          })
                        }
                      >
                        <img
                          src={item.image || "/placeholder.svg?height=48&width=48&query=" + item.name}
                          alt={item.name}
                          className="w-full h-full object-cover"
                        />
                        <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
                          <ZoomIn className="w-4 h-4 text-white" />
                        </div>
                      </div>

                      <div className="flex-1 min-w-0">
                        <p className="font-medium text-sm truncate">{item.name}</p>
                        <p className="text-xs text-muted-foreground">{item.price} ₽</p>
                      </div>

                      <div className="flex items-center gap-2">
                        {qty > 0 ? (
                          <>
                            <button
                              onClick={() => updateExtra(item, -1)}
                              className="w-7 h-7 rounded-full bg-muted flex items-center justify-center hover:bg-muted/80 transition-colors"
                            >
                              <Minus className="w-3.5 h-3.5" />
                            </button>
                            <span className="w-6 text-center font-medium text-sm">{qty}</span>
                            <button
                              onClick={() => updateExtra(item, 1)}
                              className="w-7 h-7 rounded-full bg-primary text-primary-foreground flex items-center justify-center hover:bg-primary/90 transition-colors"
                            >
                              <Plus className="w-3.5 h-3.5" />
                            </button>
                          </>
                        ) : (
                          <button
                            onClick={() => updateExtra(item, 1)}
                            className="px-3 py-1.5 rounded-full bg-primary text-primary-foreground text-xs font-medium hover:bg-primary/90 transition-colors"
                          >
                            Добавить
                          </button>
                        )}
                      </div>
                    </div>
                  )
                })}
              </div>
            )}
          </div>
        )
      })}

      {imageModal && (
        <ImageModal
          image={imageModal.image}
          name={imageModal.name}
          ingredients={imageModal.ingredients}
          description={imageModal.description}
          nutrition={imageModal.nutrition}
          onClose={() => setImageModal(null)}
          onAdd={() => {
            const item = Object.values(availableExtras || {})
              .flat()
              .find((e) => e.name === imageModal.name)
            if (item) {
              updateExtra(item, 1)
            }
            setImageModal(null)
          }}
        />
      )}
    </div>
  )
}
