"use client"

import { useState, useEffect } from "react"
import { format, addMonths, subMonths, startOfMonth, endOfMonth, eachDayOfInterval, isSameMonth, isSameDay, isToday, isBefore, startOfDay, addDays } from "date-fns"
import { ru } from "date-fns/locale"
import { GripVertical, ChevronLeft, ChevronRight, Truck, CalendarIcon, ChefHat } from "lucide-react"
import { Button } from "@/components/ui/button"
import { cn } from "@/lib/utils"
import { Sheet, SheetContent, SheetHeader, SheetTitle, SheetTrigger } from "@/components/ui/sheet"
import { Order } from "@/lib/types"

interface CalendarProps {
  selectedDate: Date | null
  onSelectDate: (date: Date) => void
  orders?: Order[]
  onMoveOrder?: (fromDate: Date, toDate: Date) => void
  onDateClick?: (date: Date) => void
}

export function Calendar({ orders = [], onDateClick, onSelectDate, onMoveOrder, selectedDate: propSelectedDate }: CalendarProps) {
  const [currentMonth, setCurrentMonth] = useState(new Date())
  const [isMounted, setIsMounted] = useState(false)

  useEffect(() => {
    setIsMounted(true)
  }, [])

  // Avoid hydration mismatch by not rendering date-dependent content on server
  if (!isMounted) {
    return (
        <div className="space-y-4">
            <div className="bg-card rounded-3xl border shadow-sm p-4 h-[400px] flex items-center justify-center">
                <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary"></div>
            </div>
        </div>
    )
  }
  
  const handleDateClick = (date: Date) => {
      if (onDateClick) onDateClick(date)
      if (onSelectDate) onSelectDate(date)
  }

  const days = eachDayOfInterval({
    start: startOfMonth(currentMonth),
    end: endOfMonth(currentMonth),
  })

  const nextMonth = () => setCurrentMonth(addMonths(currentMonth, 1))
  const prevMonth = () => setCurrentMonth(subMonths(currentMonth, 1))

  const getOrderForDate = (date: Date) => {
    return orders.find((order) => {
      const orderDate = new Date(order.startDate)
      return (
        orderDate.getDate() === date.getDate() &&
        orderDate.getMonth() === date.getMonth() &&
        orderDate.getFullYear() === date.getFullYear()
      )
    })
  }
  
  const hasFoodForDate = (date: Date) => {
      const checkDate = new Date(date)
      checkDate.setHours(0,0,0,0)
      
      return orders.some(order => {
           const deliveryDate = new Date(order.startDate)
           deliveryDate.setHours(0,0,0,0)
           
           const day1 = new Date(deliveryDate)
           day1.setDate(day1.getDate() + 1)
           
           const day2 = new Date(deliveryDate)
           day2.setDate(day2.getDate() + 2)
           
           return checkDate.getTime() === day1.getTime() || checkDate.getTime() === day2.getTime()
      })
  }

  const renderDayCell = (date: Date) => {
    const isSelected = propSelectedDate ? isSameDay(date, propSelectedDate) : false
    const isCurrentMonth = isSameMonth(date, currentMonth)
    const isDateToday = isToday(date)
    const order = getOrderForDate(date)
    const hasOrder = !!order
    const hasFood = hasFoodForDate(date)
    
    const today = startOfDay(new Date())
    const isPast = isBefore(date, today)
    const canOrder = !isPast

    return (
      <div
        key={date.toISOString()}
        onClick={() => handleDateClick(date)}
        className={cn(
          "relative h-14 sm:h-16 flex flex-col items-center justify-center rounded-xl transition-all border-2",
          !isCurrentMonth && "invisible",
          isSelected
            ? "border-primary bg-primary/5 shadow-inner"
            : "border-transparent",
          !isSelected && !hasOrder && !hasFood && canOrder && "hover:bg-secondary/50",
          hasOrder
            ? "bg-primary text-primary-foreground shadow-lg cursor-pointer hover:scale-105 active:scale-95 border-primary"
            : hasFood
            ? "bg-green-100 hover:bg-green-200 dark:bg-green-900/20 dark:hover:bg-green-900/40 text-green-700 dark:text-green-300 font-medium border-green-200 dark:border-green-800"
            : canOrder
            ? "cursor-pointer bg-card"
            : "opacity-30 cursor-not-allowed bg-muted/50",
        )}
      >
        <span className={cn(
            "text-sm sm:text-base font-medium",
             hasOrder ? "text-primary-foreground" : ""
        )}>
            {format(date, "d")}
        </span>
        
        {isDateToday && !hasOrder && !hasFood && (
           <span className="text-[10px] text-primary font-medium mt-0.5">Сегодня</span>
        )}

        <div className="flex items-center gap-1 mt-1">
          {hasOrder && (
            <Truck className="w-3.5 h-3.5 sm:w-4 sm:h-4 animate-pulse" />
          )}
          
          {hasFood && (
             <ChefHat className={cn("w-3.5 h-3.5 sm:w-4 sm:h-4", hasOrder ? "opacity-90" : "opacity-70")} />
          )}
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-4">
      <div className="bg-card rounded-3xl border shadow-sm p-4">
        <div className="flex items-center justify-between mb-4">
          <Button variant="ghost" size="icon" onClick={prevMonth} className="hover:bg-secondary rounded-full">
            <ChevronLeft className="w-5 h-5" />
          </Button>
          <h2 className="font-semibold text-lg capitalize">
            {format(currentMonth, "LLLL yyyy", { locale: ru })}
          </h2>
          <Button variant="ghost" size="icon" onClick={nextMonth} className="hover:bg-secondary rounded-full">
            <ChevronRight className="w-5 h-5" />
          </Button>
        </div>

        <div className="grid grid-cols-7 gap-1 sm:gap-2 mb-2">
          {["Пн", "Вт", "Ср", " Чт", "Пт", "Сб", "Вс"].map((day) => (
            <div key={day} className="text-center text-xs font-medium text-muted-foreground py-1">
              {day}
            </div>
          ))}
        </div>

        <div className="grid grid-cols-7 gap-1 sm:gap-2">
          {days.map(renderDayCell)}
        </div>
        
        {/* Legend */}
        <div className="flex items-center gap-4 mt-4 pt-4 border-t text-xs text-muted-foreground justify-center">
            <div className="flex items-center gap-1.5">
                <div className="w-3 h-3 bg-primary rounded-full animate-pulse" />
                <span>Доставка</span>
            </div>
            <div className="flex items-center gap-1.5">
                <div className="w-3 h-3 bg-green-100 border border-green-200 rounded-full" />
                <span>Есть еда</span>
            </div>
        </div>
      </div>
    </div>
  )
}
